.header-burger {
  display: none;
  border: none;
  padding: 0;
  position: absolute;
  align-self: flex-end;
  right: 0px;
  bottom: 30px;
  width: 30px;
  height: 24px;
  cursor: pointer;
  background-color: transparent;
}

.header-burger:hover {
    opacity: .7;
}

@media screen and (max-width: 767px) {
  .header-burger {
    display: block;    
  }
}
.header-burger__line {
  display: block;
  height: 3px;
  width: 24px;
  background-color: #fff;
  transition: .2s all;
  margin: 0 auto 6px;
}
.header-burger__line_active {
    margin-bottom: -1px;
}

.header-burger__line_active:nth-child(1) {
    -webkit-transform: translateY(1px) rotate(-45deg);
            transform: translateY(1px) rotate(-45deg);
  }
  
  .header-burger__line_active:nth-child(2) {
    display: none;
  }
  
  .header-burger__line_active:nth-child(3) {
    -webkit-transform: translateY(-1px) rotate(45deg);
            transform: translateY(-1px) rotate(45deg);
  }
