.footer {
  width: 100%;  
  margin: 0 auto;
  padding-bottom: 60px;
  display: flex;
  flex-shrink: 0;
}

@media screen and (max-width: 1023px) {
  .footer {
    padding-bottom: 48px;    
  }
}

@media screen and (max-width: 767px) {
  .footer {
    padding-bottom: 36px;    
  }
}

.footer__text {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  color: #545454;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 1023px) {
  .footer__text {
    font-size: 16px;
  }
}

@media screen and (max-width: 767px) {
  .footer__text {
    font-size: 14px;
  }
}

.popup {
  display: flex;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;  
  top: 0;
  left: 0;
  z-index: 10;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s linear;
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__container {
  display: block;
  width: 358px;
  padding: 34px 36px 36px 36px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
  z-index: 1;
  margin: 0;
  background-color: #fff;
  color: #000;
}

@media screen and (max-width: 1023px) {
  .popup__container {
    max-width: 315px;
    padding: 29px;
  }
}

@media screen and (max-width: 767px) {
  .popup__container {
    max-width: 282px;
    margin: 0 19px;
    padding: 25px 22px;
  }
}

.popup__btn-submit {
  width: 100%;
  height: 50px;
  margin-top: 18px;
  font-style: normal;
  font-weight: 400;
  padding: 0;
  border: none;
  border-radius: 2px;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  background-color: #000000;
  color: #ffffff;
  transition: all 0.2s ease-in;
  cursor: pointer;
}

.popup__btn-submit:hover {
  opacity: 0.6;
}

@media screen and (max-width: 767px) {
  .popup__btn-submit {
    height: 46px;
    margin-top: 15px;
    font-size: 14px;
  }
}

.popup__btn-submit_disabled {
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.2);
  color: #c4c4c4;
  cursor: default;
}

.popup__btn-submit_disabled:hover {
  opacity: 1;
}

.popup__field {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  justify-content: center;
  border: none;
  padding: 0 0 12px;
  margin: 0;
  width: 100%;
  outline: none;
  white-space: nowrap;
  overflow: hidden;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
}

.popup__field_invalid {
  border-bottom: 1px solid red;
}

.popup__field_top {
    padding-top: 16px;
}

@media screen and (max-width: 1023px) {
    .popup__field_top {
        padding-top: 26px;   
    }
  }
  
  @media screen and (max-width: 767px) {
    .popup__field_top {
        padding-top: 33px;    
    }
  }
.popup__btn-close {
  background-image: url(/react-mesto-auth/static/media/Close.4c6a2169.svg);
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  position: absolute;
  border: none;
  padding: 0;
  top: -40px;
  right: -40px;
  z-index: 1;
  transition: opacity 0.2s ease-in-out;
  -webkit-transition: opacity 0.2s ease-in-out;
  -moz-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
}

.popup__btn-close:hover {
  opacity: 0.6;
}

@media screen and (max-width: 767px) {
  .popup__btn-close {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0;
  }
}

.popup__title {
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.2;
  padding: 0 0 38px 0;
  margin: 0;
}

@media screen and (max-width: 1023px) {
  .popup__title {
    font-size: 22px;    
  }
}

@media screen and (max-width: 767px) {
  .popup__title {
    font-size: 20px;    
  }
}

.popup__title_info {
    padding-bottom: 24px;
    text-align: center;   
}
.popup__image {
    display: block;
    box-sizing: border-box;
    width: 120px;
    height: 120px;
    margin: 26px auto 32px;    
}
.avatar-popup {
    background-color: rgba(0, 0, 0, 0.5);
}
.confirm-popup {
    background-color: rgba(0, 0, 0, 0.5);
}
.edit-popup {
  background-color: rgba(0, 0, 0, 0.5);
}

.info-popup {
  background-color: rgba(0, 0, 0, .5);
}
.add-popup {
  background-color: rgba(0, 0, 0, 0.5);
}

.photo-popup {
  background-color: rgba(0, 0, 0, 0.9);
}

.photo-popup__container {
  max-width: 75vw;
  display: block;
  position: relative;
  padding: 0;
  z-index: 1;
}

.photo-popup__image {
  max-width: 100%;
  max-height: 75vh;
  object-fit: cover;
  margin: 0;
  padding: 0;
}

.photo-popup__caption {
  font-size: 12px;
  font-weight: 400;
  line-height: 1.25;
  text-align: left;
  font-style: normal;
  color: #ffffff;
  padding: 0;
  margin-top: 10px;
  z-index: 1;
}


